// import { Link } from "@reach/router";
import emailjs from "emailjs-com";
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import SendIcon from '@material-ui/icons/Send';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import InstagramIcon from '@material-ui/icons/Instagram';
import GitHubIcon from '@material-ui/icons/GitHub';
import FacebookIcon from '@material-ui/icons/Facebook';
import { useState } from "react";
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Instagram from "../img/instagram.svg";
import LinkedIn from "../img/linkedin.svg";
import GitHub from "../img/github.svg";
// import { FormatStrikethroughOutlined } from "@material-ui/icons";

const Contact = () => {
    const [open, setOpen] = useState(false);
    
    //Alert When Submitting Form
    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    //E-Mail 
    const sendEmail = (e) => {
        e.preventDefault();

    emailjs.sendForm('service_id_408', 'template_xybwhrj', e.target, 'user_guRwP61T6MuvOcNdaBWhn')
        .then((result) => {
            console.log(result.text);
        }, (error) => {
            console.log(error.text);
        });
        e.target.reset();
    }

    // Icon Style
    const useStyles = makeStyles((theme) => ({
        button: {
            margin: theme.spacing(1),
            width:120,
            height:40,
            left: -8,
            fontSize: 18
        }
    }));
    
    const classes = useStyles();

    return (
        <>    
            <div className="contact-text">
                <h1 className="contact-h1">Contact Me</h1>
            </div>
            <div className="icons">
                <div className="linkedin-icon">
                    <img src={LinkedIn} onClick={event => window.location.href='https://www.linkedin.com/in/hussein-guevara/'}/>
                    <h3 className="linkedin-h">Connect On Linked In</h3>
                </div>
                <div className="instagram-icon">
                    <img src={Instagram} onClick={event => window.location.href='https://www.instagram.com/da90skidd/'}/>    
                    <h3 className="instagram-h">Follow Me On Instagram</h3>
                </div>    
                <div className="github-icon">
                    <img src={GitHub} onClick={event =>  window.location.href='https://github.com/HusseinGuevara'}/>
                    <h3 className="github-h">Check Me Out On GitHub</h3>
                </div>    
            </div>    
            <div className="email-container">
                <form className="contact-form" onSubmit={sendEmail}>
                    <TextField className="subject-line contact" label="Subject" name="subject" variant="outlined" />
                    <TextField className="email-line contact" label="E-Mail" name="email" type="email" variant="outlined" />
                    <TextField className="name-line contact" label="Name" name="name" variant="outlined"/>
                    <TextField className="message-line contact" label="Message" name="message" variant="outlined"  multiline
                    rows={4} maxRows={4}/>
                    <Button
                        variant="contained"
                        color="primary"
                        type="submit"
                        className={classes.button}
                        endIcon={<SendIcon/>}
                        onClick={handleClickOpen}>
                        Send
                    </Button>
                </form>

                <Dialog
                    open={open}
                    onClose={handleClose}
                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"
                >
                    <DialogTitle id="alert-dialog-title">{"Thank You For Your Interest!"}</DialogTitle>
                    <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        Your E-Mail has been sent. Please expect a response within 1-2 days. 
                    </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                    <Button onClick={handleClose} color="primary" autoFocus>
                        Close 
                    </Button>
                    </DialogActions>
                </Dialog>

            </div>    
        </>        
    );



    
}
export default Contact
